<template>
	<div class="tree">
		<tree-node :tree-data="treeData" :func='nodeClick' :expand='expandClick'></tree-node>
	</div>
</template>

<script>
	import TreeNode from './tree-node';
	import { Tree } from './tree';

	export default {
		name: "tree",
		components: {
			TreeNode
		},
		props: {
			treeData: Array,
		},
		watch: {
			treeData: {
				handler: function(data) {
					// this.treeData = Tree.selectIcon(data);
					// this.treeDataA = Tree.initTreeData(this.treeData);
				},
				deep: true
			}
		},
		created() {
			this.isTree = true;
			// Tree.generateKey(this.TreeData, 0);
			// this.treeDataA = Tree.initTreeData(this.treeData);
		},
		data() {
			return {
				store: {
					root: [],
				},
				treeDataA: [],
				treeDeepCopy: [],
			};
		},
		methods: {
			initData() {},
			// 点击节点
			nodeClick: function(m, parent, trees) {
				this.treeDeepCopy = trees;
				this.nodeModel = m; // 当前点击节点对象
				this.parentNodeModel = parent; //当前点击节点父亲对象
			},
			// 点击展开收起
			expandClick: function(m) {
				console.log(m)
			}
		}
	};
</script>

<style lang="scss" scoped>
	.tree {
		padding: 20px;
		height: 650px;
		overflow: auto;
		background: #FFFFFF;
		overflow-y: scroll;
	}
</style>


